import { async } from "regenerator-runtime";
import * as de from "./domElements"
const storage = window.localStorage
const UT = require("unixtimejs");

export const countryScroll = (function(){
let first = true
let index = -1
const container = de.autocomplete_holder

function clearSelected(){
Array.from(container.children)
  .forEach(countryDiv => {
    countryDiv.classList.remove("selected")
  })
}

function selectChild(child) {
  child.classList.add("selected")
  child.scrollIntoView(false)
}

function exec(e){
  const container_children = container.children.length - 1

    if (e.key === "ArrowUp") { index-- }
    else if (e.key === "ArrowDown") { index++ }

    if (index > container_children) {
      index = 0
    }
    if (index < 0 ) {
      index = container_children
    }
    if (first) {
      index = 0
      first = false
    }

    // console.log({index});
    if (e.key === "Enter") {  
      evaluateSearchBarFromDIv_nonEvent(container.children[index])
      return
    }

    const child = container.children[index]
    if (!child) return

    clearSelected()
    selectChild(child)
  }
  function reset(params) {
    index = 0
    first = true
  }
  return {exec , reset}
})()

