import * as de from "./utility/domElements" ;
import * as f from "./utility/functions"
import setUpCountries from "./utility/countries"
import { async } from "regenerator-runtime";


(function(){
  de.countries_search_bar.addEventListener("input" , f.recommendCountry)
  de.countries_search_bar.addEventListener("keydown" , f.countryScroll.exec )

  de.get_weather_button.addEventListener("pointerdown" , f.initiatWeather )

  de.daily_weather_selector.addEventListener("pointerdown" , (e)=>{
    f.selectTimeSelector(e.target)
    f.showCurrentWeather()
  })
  
  de.hourly_weather_selector.addEventListener("pointerdown" , (e)=>{
    f.selectTimeSelector(e.target)
    f.showHourlyWeather()
  })
  
})()
const storage = window.localStorage

if(!storage.countries){setUpCountries()}








